import React, { useCallback, useEffect } from 'react';
import { Provider } from 'react-redux';
import { store } from '../redux/store';
import { Store } from '@reduxjs/toolkit';
import { RootState } from '../redux/store/rootReducer';
import { setMedia } from '../redux/features/app/actions';
import { getMedia } from '../api/endpoint';
import dayjs from 'dayjs';

const AuthProvider = ({
  children,
}: {
  children: (data: Store<RootState>) => React.ReactNode;
}) => {
  const fetchMediaJson = useCallback(async () => {
    try {
      const response = await fetch(`${getMedia}?key=${dayjs().valueOf()}`);
      const data = await response.json();
      store.dispatch(setMedia(data));
    } catch (error) {
      console.error('error fetchMediaJson', error);
    }
  }, []);

  useEffect(() => {
    fetchMediaJson();
  }, [fetchMediaJson]);

  return <Provider store={store}>{children(store)}</Provider>;
};

export default AuthProvider;
